import React from 'react'
import "./Jammy.css";

function Jammy() {
    return (
    <div className="Jam">
        <div className="Jammy__background">
            <div className="Jammy__greeting">
                <h2>Thank you for visiting my page!</h2>
                <div className="Jammy__instruction">
                    <h3>This is a functional and working Amazon clone website</h3>
                    <p>Everything you see is an imitation of an actual Amazon Website</p>
                    <p>When using 'Sign In', make sure to input fake email and click on "Create your Amazon Account" </p>
                    <div className="Jammy__payment">
                        <p>When adding something onto basket and checking out payment, use dummy payment of '42' and keep typying 42 until no more.</p>
                        <p>This is a legit dummy credit card!</p>
                        <p> Once the field is full, select 'Buy Now' and watch the magic work!</p>
                    </div>     
                </div>
            </div>
        </div>
      </div>  
    )
}

export default Jammy
