import React from 'react'
import "./Footer.css";
import AddAPhotoIcon from '@material-ui/icons/AddAPhoto';
import FacebookIcon from '@material-ui/icons/Facebook';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import GitHubIcon from '@material-ui/icons/GitHub';



function Footer() {
    return (
        <div className="Footer">
            <div className="Footer__Facebook"><a href="https://www.facebook.com/">
            <FacebookIcon className="Footer__FacebookIcon"/></a>
                Follow me on Facebook
            </div>
            <div className="Footer__LinkedIn"><a href="https://www.linkedin.com/in/jameel-anwar-766918138/">
            <LinkedInIcon className="Footer__LinkedInIcon"/></a>
                Connect me in LinkedIn
            </div>
            <div className="Footer__Instagram"><a href="https://www.instagram.com/thisisjammya/?hl=en">
            <AddAPhotoIcon className="Footer__InstagramIcon" /></a> 
                Follow me on Instagram
            </div>
            <div className="Footer__GitHub"><a href="https://github.com/ItzJammyZz">
                <GitHubIcon className="Footer__GitHubIcon"/></a>
                Check out my Github
            </div>
        </div>
    )
}

export default Footer
